@keyframes buttonAnim {
    0% {
        left: -130%;
    }
    100% {
        left: 130%
    }
}
.default {
    color: #99ff99;
    background: #983300;
;
}
.btn {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    display: block;
    margin: 10px;
    width: 80px;
    font-size: 14px;
    text-align: center;
    border: 1px solid black
}
.btn:after {
    background: #fff;
    content: "";
    opacity: 0.8;
    position: absolute;
    left: -130%;
    top: 0;
    height: 100%;
    width: 10px;

    transform: rotate(45deg);

    /*z-index: 1;*/
}
.btn:hover:after {
    animation: buttonAnim 0.5s ease;
}
.btn:hover::before {
    background: #fff;
    content: "";
    opacity: 0.2;
    position: absolute;
    left: -100vw;
    top: 0;
    height: 100vh;
    width: 300vw;
}


.btn:disabled {
    background-color: gray;
    opacity: 0.5;
}
.btn::after:disabled {
    z-index: -1;
}
.btn:disabled::before {
    z-index: -1;
}
.red {
    background: red;
    color: black;
}