.message {
    display: flex;
    align-items: flex-end;
    margin: 10px;
    background-color: greenyellow;
    max-width: 500px;
}
.messageAvatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.time {
    display: flex;
    justify-content: flex-end;
    color: #aaa;
}
.messageWindow {
    margin-left: 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: black;
}

.messageName {
    color: cornflowerblue;
}
.messageMessage {
 min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;
    color: white;
}
