.wrapper {
    height: 50px;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.input:focus {
    outline: none;
    border: 2px solid #983300;
}
.superInput {
    background: #983300;
    color: #99ff99;
}
.errorInput {
    background: #983300;
    color: #99ff99;
    border: 2px solid red;
}
.error {
    display: block;
    height: 20px;
    color: red;
}